import React from 'react'

class Tags extends React.Component{
	constructor(){
			super()

			this.state = {
				isOn:false,
				tags:['Urgent', 'Internal','Design','Add Functionality' ]
			}
	}
	render(){
		return (
			<ul>
			  {this.state.tags.map((tag,i) => {
				 return (
					 <li index="i">
					 		<button className ={isTagOn} name={tag} value={tag} onClick={this.addTag,this.isOn}>{tag} </button>
						</li>
					)
				})}
			</ul>
		)
	}


}

export default Tags



